
.tagLine {
  /* display: flex; */
  justify-content: center;
  margin-top: 10px;
}

#faizan {
  width: 0ch;
  overflow: hidden;
  white-space: nowrap;
  animation: tagline 10s  infinite alternate;
  border-right: 3px solid #A3E900;
}

@keyframes tagline {
    0% {
        width: 0ch;
    }
    50% {
        width: 70.2ch;
    }
}


