.run{
  /* color: black; */
  font-size: 50px;
  font-weight: bold;
  font-family: monospace;
  letter-spacing: 7px;
  cursor: pointer;
  /* width: 100%; */
}
.run span{
  transition: .5s linear;
  color: #A3E900;
}
.run:hover span:nth-child(1){
  margin-right: 5px
}
.run:hover span:nth-child(2){
  margin-left: 30px
}
.run:hover span{
  color: #A3E900;
  text-shadow: 0 0 10px #A3E900,
               /* 0 0 20px #A3E900,  */
               0 0 40px #A3E900;
}


@media screen and (max-width: 800px) {
  .mainBox {
      gap: 30px;
  }

  .box {
      min-height: 180px;
  }

  textarea {
      min-height: 180px;
  }
}

@media screen and (max-width: 600px) {
  .mainBox {
      grid-template-columns: repeat(1, 1fr);
      gap: 60px;
  }

  .box {
      min-height: 150px;
      margin-top: 20px;
  }

  textarea {
      min-height: 150px;
  }
}

@media screen and (max-width: 400px) {
  .navbar>h1{
      font-size: 28px
  }
}



/* floating pic */

.floating {  
  animation-name: floating;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
  margin-left: 30px;
  margin-top: 5px;
}

@keyframes floating {
  from { transform: translate(0,  0px);
    box-shadow: 0 5px 25px 0px rgba(0,0,0,1);
   }
  65%  { transform: translate(0, 20px);
    box-shadow: 0 5px 35px 0px rgba(0,0,0,1); }
  to   { transform: translate(0, -0px); }    

}
